"use client";

import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Bot,
  Sparkles,
  Loader2,
  Copy,
  Check,
  Send,
  PenTool,
  RotateCcw,
  History,
  ChevronLeft,
  ChevronRight,
  Maximize2,
  Minimize2,
  Wand2,
  Briefcase,
  Coffee,
  Zap,
  Heart,
  Feather,
  Layout,
  Type,
  AlignLeft,
  MoreHorizontal,
  X,
  Trash2,
  Save,
  SplitSquareHorizontal,
  Sliders,
  ChevronDown
} from "lucide-react";

// --- Types ---

interface EmailState {
  recipient: string;
  subject: string;
  keyPoints: string;
  tone: string;
  style: string;
  length: string;
  options: {
    includeCTA: boolean;
    addUrgency: boolean;
    humanize: boolean;
    formal: boolean;
    simplify: boolean;
    addWarmth: boolean;
  };
}

interface DraftVersion {
  id: string;
  content: string;
  timestamp: Date;
  label: string;
}

interface SelectOption {
  label: string;
  value: string;
  icon: string;
  description: string;
}

// --- Configuration Constants ---

const TONES = [
  { id: "Professional", icon: Briefcase, color: "text-[#00C26D]", bg: "bg-[#00C26D]/10", border: "border-[#00C26D]/20" },
  { id: "Casual", icon: Coffee, color: "text-[#00C26D]", bg: "bg-[#00C26D]/10", border: "border-[#00C26D]/20" },
  { id: "Urgent", icon: Zap, color: "text-[#00C26D]", bg: "bg-[#00C26D]/10", border: "border-[#00C26D]/20" },
  { id: "Empathetic", icon: Heart, color: "text-[#00C26D]", bg: "bg-[#00C26D]/10", border: "border-[#00C26D]/20" },
];

const VOICE_OPTIONS: SelectOption[] = [
  { label: "Direct", value: "Direct", icon: "âš¡", description: "Sharp, clear, to the point" },
  { label: "Balanced", value: "Balanced", icon: "âš–ï¸", description: "Neutral and smooth" },
  { label: "Friendly", value: "Friendly", icon: "ðŸ˜Š", description: "Warm and approachable" },
  { label: "Bold", value: "Bold", icon: "ðŸ”¥", description: "Strong and confident" },
  { label: "Soft", value: "Soft", icon: "ðŸŒ™", description: "Gentle and relaxed" },
  { label: "Executive", value: "Executive", icon: "ðŸ§Š", description: "High-level, concise, senior tone" }
];

const LENGTH_OPTIONS: SelectOption[] = [
  { label: "Very Short", value: "Very Short", icon: "âœ‚ï¸", description: "Ultra concise" },
  { label: "Short", value: "Short", icon: "ðŸª¶", description: "Brief and efficient" },
  { label: "Medium", value: "Medium", icon: "ðŸ“„", description: "Standard email length" },
  { label: "Long", value: "Long", icon: "ðŸ“œ", description: "More depth and detail" },
  { label: "Expanded", value: "Expanded", icon: "ðŸš€", description: "Full pitch-level detail" }
];

// --- Logic Helper: Simulate Intelligent Rewrite ---
// This ensures we get clean, standalone output without stacking labels in the body
const generateMockRewrite = (
  baseData: EmailState, 
  instruction: string, 
  originalContent: string
): string => {
  const recipient = baseData.recipient || "there";
  const subject = baseData.subject || "Follow up";
  const points = baseData.keyPoints || "the items we discussed";

  // Simulate rewriting by generating a completely fresh body based on the intent
  // This replaces the old content entirely, preserving the "rewrite" mental model
  if (instruction === "Shorten") {
    return `Subject: ${subject}\n\nHi ${recipient},\n\nJust checking in on: ${points}.\n\nLet me know your thoughts.\n\nBest,\n[Your Name]`;
  }
  
  if (instruction === "Expand") {
    return `Subject: ${subject}\n\nDear ${recipient},\n\nI hope you are having a productive week.\n\nI wanted to take a moment to elaborate on the points we touched upon recently:\n\n${points}\n\nCould we schedule some time to discuss this in greater detail? I believe there is significant opportunity here.\n\nLooking forward to your response.\n\nWarm regards,\n[Your Name]`;
  }

  if (instruction === "Formal") {
    return `Subject: ${subject}\n\nDear ${recipient},\n\nI am writing to formally address the following matters: ${points}.\n\nPlease review at your earliest convenience and advise on the next steps.\n\nSincerely,\n[Your Name]`;
  }

  if (instruction === "Punchy") {
    return `Subject: ${subject}\n\n${recipient} â€”\n\nQuick update on ${points}.\n\nReady to move when you are.\n\nCheers,\n[Your Name]`;
  }

  // Fallback / Simplify
  return `Subject: ${subject}\n\nHi ${recipient},\n\nHere is a simple summary regarding ${points}.\n\nLet's chat soon.\n\nThanks,\n[Your Name]`;
};

// --- Utilities ---

function formatOutput(item: any): string {
  if (item === null || item === undefined) return "";
  if (typeof item === "string") return item;
  
  if (Array.isArray(item)) {
    return item.map(formatOutput).join("\n\n");
  }

  if (typeof item === "object") {
    if (item.introduction || item.pain_points || item.offer || item.call_to_action) {
        return [
            item.headline, 
            item.introduction,
            item.pain_points,
            item.offer,
            item.call_to_action,
            item.ps 
        ].filter(Boolean).map(s => String(s)).join("\n\n");
    }
    if (item.result) return formatOutput(item.result);
    if (item.content) return formatOutput(item.content);
    if (item.email) return formatOutput(item.email);
    if (item.body) return formatOutput(item.body);
    
    return JSON.stringify(item, null, 2).replace(/["{}]/g, "");
  }
  return String(item);
}

// --- Main Component ---

export default function EmailAssistantPage() {
  // --- State ---
  const [formData, setFormData] = useState<EmailState>({
    recipient: "",
    subject: "",
    keyPoints: "",
    tone: "Professional",
    style: "Direct",
    length: "Medium",
    options: {
      includeCTA: false,
      addUrgency: false,
      humanize: true,
      formal: false,
      simplify: false,
      addWarmth: false,
    },
  });

  const [isLoading, setIsLoading] = useState(false);
  const [isRewriting, setIsRewriting] = useState(false);
  const [versions, setVersions] = useState<DraftVersion[]>([]);
  const [activeVersionIdx, setActiveVersionIdx] = useState<number>(-1);
  const [showHistory, setShowHistory] = useState(false);
  const [copied, setCopied] = useState(false);
  const [historyList, setHistoryList] = useState<DraftVersion[]>([]);
  
  // UI State
  const [isRefinementsOpen, setIsRefinementsOpen] = useState(false);
  const [activeRewriteTool, setActiveRewriteTool] = useState<string | null>(null);

  // --- Handlers ---

  const handleInputChange = (field: keyof EmailState, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const handleOptionToggle = (key: keyof typeof formData.options) => {
    setFormData((prev) => ({
      ...prev,
      options: { ...prev.options, [key]: !prev.options[key] },
    }));
  };

  const handleGenerate = async () => {
    setIsLoading(true);
    setActiveRewriteTool(null);
    
    const variationSeed = crypto.randomUUID();

    try {
      // Mock API Latency
      setTimeout(() => {
          const mockContent = `Subject: ${formData.subject || "Meeting Request"}\n\nHi ${formData.recipient || "there"},\n\nI hope this email finds you well.\n\nRegarding the points: ${formData.keyPoints || "No context provided."}\n\nLooking forward to connecting.\n\nBest,\n[Your Name]`;
          
          const newVersion: DraftVersion = {
            id: variationSeed,
            content: mockContent,
            timestamp: new Date(),
            label: `Draft ${versions.length + 1}`
          };
          
          const newVersions = [...versions, newVersion];
          setVersions(newVersions);
          setActiveVersionIdx(newVersions.length - 1);
          setHistoryList(prev => [newVersion, ...prev]);
          setIsLoading(false);
      }, 1500);

    } catch (error) {
      console.error("Generation failed", error);
      setIsLoading(false);
    }
  };

  const handleRewrite = async (label: string, instruction: string) => {
    if (activeVersionIdx === -1) return;
    setIsRewriting(true);
    setActiveRewriteTool(label);

    const currentContent = versions[activeVersionIdx].content;
    const variationSeed = crypto.randomUUID();

    try {
       // Logic Fix: Use helper to generate clean, standalone rewrite
       setTimeout(() => {
          const cleanRewrite = generateMockRewrite(formData, instruction, currentContent);

          const newVersion: DraftVersion = {
            id: variationSeed,
            content: cleanRewrite, // âœ… Clean content only
            timestamp: new Date(),
            label: `Rewrite: ${label}` // âœ… Label in metadata only
          };
          
          const newVersions = [...versions, newVersion];
          setVersions(newVersions);
          setActiveVersionIdx(newVersions.length - 1);
          setHistoryList(prev => [newVersion, ...prev]); // Add to history sidebar too
          setIsRewriting(false);
       }, 1000);
    } catch (e) {
      console.error(e);
      setIsRewriting(false);
    }
  };

  const copyText = () => {
    if (activeVersionIdx === -1) return;
    navigator.clipboard.writeText(versions[activeVersionIdx].content);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const clearAll = () => {
    setVersions([]);
    setActiveVersionIdx(-1);
    setActiveRewriteTool(null);
    setFormData({ ...formData, keyPoints: "", subject: "", recipient: "" });
  };

  // --- Render ---

  return (
    <div className="h-full w-full bg-[#0B1120] text-slate-200 font-sans selection:bg-[#00C26D]/30 selection:text-white overflow-hidden flex flex-col">
      
      {/* Header */}
      <header className="flex justify-between items-center px-6 py-4 border-b border-zinc-800 bg-[#0B1120] shrink-0">
        <div className="flex items-center gap-4">
          <div className="p-2 rounded-xl bg-[#1E293B] border border-slate-700/50 shadow-lg shadow-green-900/10">
            <Bot className="w-5 h-5 text-[#00C26D]" />
          </div>
          <div>
            <h1 className="text-xl font-bold text-white tracking-tight">Email Assistant</h1>
            <p className="text-xs text-slate-400 font-medium">Enterprise communication engine</p>
          </div>
        </div>
        <button 
          onClick={() => setShowHistory(!showHistory)}
          className={`p-2.5 rounded-lg border transition-all duration-300 ${
            showHistory 
              ? 'bg-[#00C26D]/10 border-[#00C26D] text-[#00C26D]' 
              : 'bg-[#1E293B] border-slate-700 text-slate-400 hover:text-white'
          }`}
        >
          <History className="w-4 h-4" />
        </button>
      </header>

      <div className="flex-1 flex overflow-hidden p-6 gap-6">
        
        {/* === LEFT PANEL: SMART INPUT ENGINE === */}
        <div className="w-[380px] shrink-0 flex flex-col gap-4 overflow-hidden">
            <div className="flex-1 overflow-y-auto pr-2 space-y-4 scrollbar-thin scrollbar-thumb-zinc-700">
                
                {/* Context Block */}
                <div className="bg-[#1E293B] border border-slate-700/50 rounded-xl p-4 shadow-lg">
                  <div className="flex items-center gap-2 mb-3">
                    <Sparkles className="w-3.5 h-3.5 text-[#00C26D]" />
                    <span className="text-[11px] font-bold text-slate-200 uppercase tracking-wide">Context</span>
                  </div>
                  <div className="space-y-3">
                    <Input label="Recipient" placeholder="e.g. Elon Musk" value={formData.recipient} onChange={(v: string) => handleInputChange("recipient", v)} />
                    <Input label="Subject" placeholder="e.g. Partnership" value={formData.subject} onChange={(v: string) => handleInputChange("subject", v)} />
                    <div>
                      <label className="block text-[10px] font-bold text-slate-400 uppercase mb-1.5 ml-1 tracking-wider">Key Points</label>
                      <textarea
                        value={formData.keyPoints}
                        onChange={(e) => handleInputChange("keyPoints", e.target.value)}
                        className="w-full bg-[#0F172A] border border-slate-700 rounded-lg p-3 text-[12px] text-slate-200 h-24 resize-none focus:border-[#00C26D]/50 focus:ring-1 focus:ring-[#00C26D]/50 outline-none placeholder-slate-600"
                        placeholder="- Ask for a meeting..."
                      />
                    </div>
                  </div>
                </div>

                {/* Tone & Style Block */}
                <div className="bg-[#1E293B] border border-slate-700/50 rounded-xl p-4 shadow-lg">
                   <div className="flex items-center gap-2 mb-3">
                    <Feather className="w-3.5 h-3.5 text-[#00C26D]" />
                    <span className="text-[11px] font-bold text-slate-200 uppercase tracking-wide">Style</span>
                  </div>
                  
                  <div className="grid grid-cols-2 gap-2 mb-3">
                    {TONES.map((t) => (
                      <button
                        key={t.id}
                        onClick={() => handleInputChange("tone", t.id)}
                        className={`flex items-center gap-2 p-2 rounded-lg border transition-all ${
                          formData.tone === t.id
                            ? `bg-[#00C26D]/10 border-[#00C26D]`
                            : "bg-transparent border-slate-700 hover:bg-slate-700/50 opacity-70 hover:opacity-100"
                        }`}
                      >
                        <t.icon className={`w-3 h-3 ${formData.tone === t.id ? 'text-[#00C26D]' : 'text-slate-400'}`} />
                        <span className={`text-[11px] font-medium ${formData.tone === t.id ? 'text-white' : 'text-slate-400'}`}>{t.id}</span>
                      </button>
                    ))}
                  </div>

                  <div className="space-y-3">
                    <Select label="Voice" value={formData.style} options={VOICE_OPTIONS} onChange={(v: string) => handleInputChange("style", v)} />
                    <Select label="Length" value={formData.length} options={LENGTH_OPTIONS} onChange={(v: string) => handleInputChange("length", v)} />
                  </div>
                </div>

                {/* Refinements Block */}
                <div className="bg-[#1E293B] border border-slate-700/50 rounded-xl p-4 shadow-lg">
                   <div onClick={() => setIsRefinementsOpen(!isRefinementsOpen)} className="flex items-center justify-between cursor-pointer group">
                      <div className="flex items-center gap-2">
                        <Sliders className="w-3.5 h-3.5 text-[#00C26D]" />
                        <span className="text-[11px] font-bold text-slate-200 uppercase tracking-wide">Fine Tuning</span>
                      </div>
                      <ChevronDown className={`w-3.5 h-3.5 text-slate-500 transition-transform ${isRefinementsOpen ? 'rotate-180' : ''}`} />
                   </div>
                   
                   <AnimatePresence>
                     {isRefinementsOpen && (
                       <motion.div
                         initial={{ height: 0, opacity: 0, marginTop: 0 }}
                         animate={{ height: "auto", opacity: 1, marginTop: 16 }}
                         exit={{ height: 0, opacity: 0, marginTop: 0 }}
                         className="overflow-hidden grid grid-cols-1 gap-2"
                       >
                          {Object.entries(formData.options).map(([key, val]) => (
                            <Toggle key={key} label={key.replace(/([A-Z])/g, ' $1').trim()} checked={val} onChange={() => handleOptionToggle(key as keyof typeof formData.options)} />
                          ))}
                       </motion.div>
                     )}
                   </AnimatePresence>
                </div>
            </div>

            {/* Sticky Generate Button */}
            <div className="pt-2">
              <button
                onClick={handleGenerate}
                disabled={isLoading || !formData.keyPoints}
                className="w-full rounded-xl bg-[#00C26D] p-4 text-white font-bold shadow-lg shadow-green-500/20 transition-all hover:bg-[#009e59] hover:scale-[1.02] active:scale-[0.98] disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
              >
                {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Sparkles className="w-4 h-4" />}
                <span className="text-sm">{versions.length > 0 ? "Generate Alternative" : "Draft Email"}</span>
              </button>
            </div>
        </div>

        {/* === CENTER/RIGHT: PREMIUM OUTPUT ENGINE === */}
        <div className={`flex-1 transition-all duration-500 ease-out flex gap-6 ${showHistory ? 'mr-[320px]' : ''}`}>
          <div className="flex-1 bg-[#1E293B] border border-slate-700/50 rounded-2xl shadow-xl flex flex-col overflow-hidden relative">
            
            {/* Window Header */}
            <div className="bg-[#0F172A] px-5 py-3 flex items-center justify-between border-b border-slate-700/50 shrink-0">
              <div className="flex gap-2">
                <div className="w-3 h-3 rounded-full bg-[#FF5F57]" />
                <div className="w-3 h-3 rounded-full bg-[#FEBC2E]" />
                <div className="w-3 h-3 rounded-full bg-[#28C840]" />
              </div>
              <div className="flex items-center gap-2 text-[11px] font-bold text-slate-500 uppercase tracking-widest">
                <PenTool className="w-3 h-3" /> Composer
              </div>
              <div className="flex items-center gap-2">
                <div className={`w-1.5 h-1.5 rounded-full ${isLoading ? 'bg-yellow-500 animate-pulse' : 'bg-[#00C26D]'} `}></div>
                <span className="text-[10px] text-[#00C26D] font-medium">{isLoading ? "GENERATING..." : "READY"}</span>
              </div>
            </div>

            {/* Version Tabs */}
            {versions.length > 0 && (
                <div className="bg-[#0F172A] border-b border-slate-700/50 px-2 flex items-center gap-1 overflow-x-auto no-scrollbar shrink-0">
                  {versions.map((v, idx) => (
                    <button
                      key={v.id}
                      onClick={() => setActiveVersionIdx(idx)}
                      className={`px-4 py-3 text-[11px] font-bold transition-all border-b-2 ${
                        activeVersionIdx === idx
                          ? "border-[#00C26D] text-white bg-slate-800"
                          : "border-transparent text-slate-500 hover:text-slate-300"
                      }`}
                    >
                      <span>V{idx + 1}</span>
                    </button>
                  ))}
                </div>
            )}

            {/* Editor Area */}
            <div className="flex-1 p-8 bg-transparent overflow-y-auto custom-scrollbar relative">
              {isLoading || isRewriting ? (
                 <div className="absolute inset-0 flex flex-col items-center justify-center bg-[#1E293B]/90 z-20 backdrop-blur-sm gap-4">
                    <Loader2 className="w-10 h-10 text-[#00C26D] animate-spin" />
                    <p className="text-slate-400 text-sm font-medium animate-pulse">Drafting content...</p>
                 </div>
              ) : activeVersionIdx !== -1 ? (
                 <div className="h-full flex flex-col">
                    <div className="prose prose-invert max-w-none text-slate-200 text-[15px] leading-relaxed whitespace-pre-wrap font-light tracking-wide flex-1">
                       {versions[activeVersionIdx].content}
                    </div>
                    
                    {/* Rewrite Toolbar */}
                    <div className="mt-8 pt-6 border-t border-slate-700/50 shrink-0">
                       <div className="flex items-center gap-2 mb-3">
                         <Wand2 className="w-3.5 h-3.5 text-[#00C26D]" />
                         <span className="text-[10px] font-bold text-slate-500 uppercase tracking-widest">AI Rewrite Tools</span>
                       </div>
                       <div className="flex flex-wrap gap-2">
                          <RewriteButton icon={Minimize2} label="Shorten" onClick={() => handleRewrite("Shorten", "Shorten this.")} disabled={isRewriting} isActive={activeRewriteTool === "Shorten"} />
                          <RewriteButton icon={Maximize2} label="Expand" onClick={() => handleRewrite("Expand", "Expand this.")} disabled={isRewriting} isActive={activeRewriteTool === "Expand"} />
                          <RewriteButton icon={Briefcase} label="Formal" onClick={() => handleRewrite("Formal", "Make it formal.")} disabled={isRewriting} isActive={activeRewriteTool === "Formal"} />
                          <RewriteButton icon={Zap} label="Punchy" onClick={() => handleRewrite("Punchy", "Make it punchy.")} disabled={isRewriting} isActive={activeRewriteTool === "Punchy"} />
                       </div>
                    </div>
                 </div>
              ) : (
                 <div className="h-full flex flex-col items-center justify-center text-slate-500/50 gap-6">
                    <div className="w-24 h-24 rounded-full bg-slate-800/50 flex items-center justify-center border border-slate-700/50">
                       <Bot className="w-10 h-10 opacity-50" />
                    </div>
                    <p className="text-sm text-slate-500">Ready to compose.</p>
                 </div>
              )}
            </div>

            {/* Bottom Actions */}
            {activeVersionIdx !== -1 && (
               <div className="p-4 bg-[#0F172A] border-t border-slate-700/50 flex justify-between items-center shrink-0">
                  <button onClick={clearAll} className="text-xs text-red-400 hover:text-white flex items-center gap-2 px-3 py-2 rounded hover:bg-red-500/20 transition-colors">
                     <Trash2 className="w-3.5 h-3.5" /> Clear
                  </button>
                  <div className="flex gap-3">
                     <button onClick={handleGenerate} className="text-xs text-slate-400 hover:text-white px-3 py-2 rounded hover:bg-slate-800 transition-colors flex items-center gap-2">
                        <RotateCcw className="w-3.5 h-3.5" /> Retry
                     </button>
                     <button onClick={copyText} className="px-4 py-2 bg-[#00C26D] hover:bg-[#009e59] text-white font-bold rounded-lg text-xs flex items-center gap-2 transition-transform active:scale-95">
                        {copied ? <Check className="w-3.5 h-3.5" /> : <Copy className="w-3.5 h-3.5" />}
                        {copied ? "Copied" : "Copy"}
                     </button>
                  </div>
               </div>
            )}
          </div>
        </div>

        {/* === RIGHT PANEL: HISTORY (Overlay) === */}
        <AnimatePresence>
          {showHistory && (
            <motion.div 
              initial={{ x: "100%" }}
              animate={{ x: 0 }}
              exit={{ x: "100%" }}
              transition={{ type: "spring", damping: 25, stiffness: 200 }}
              className="absolute right-0 top-0 bottom-0 w-[320px] bg-[#1E293B] border-l border-slate-700/50 shadow-2xl z-50 flex flex-col"
            >
               <div className="p-5 border-b border-slate-700/50 flex justify-between items-center bg-[#0F172A]">
                  <h3 className="text-xs font-bold text-slate-400 uppercase tracking-widest flex items-center gap-2">
                     <History className="w-3.5 h-3.5 text-[#00C26D]" /> History
                  </h3>
                  <button onClick={() => setShowHistory(false)}><X className="w-4 h-4 text-slate-500 hover:text-white" /></button>
               </div>
               <div className="flex-1 overflow-y-auto p-4 space-y-3 scrollbar-thin scrollbar-thumb-zinc-700">
                  {historyList.map((item, i) => (
                     <div key={item.id} onClick={() => { setVersions([...versions, item]); setActiveVersionIdx(versions.length); }} className="p-4 bg-slate-800/50 border border-slate-700/50 rounded-lg cursor-pointer hover:border-[#00C26D]/50 hover:bg-slate-800 transition-all">
                        <div className="flex justify-between mb-2">
                           <span className="text-[10px] font-bold text-[#00C26D] bg-[#00C26D]/10 px-2 py-0.5 rounded">{item.label}</span>
                           <span className="text-[10px] text-slate-500">{item.timestamp.toLocaleTimeString([], {hour:'2-digit', minute:'2-digit'})}</span>
                        </div>
                        <p className="text-xs text-slate-400 line-clamp-2">{item.content}</p>
                     </div>
                  ))}
               </div>
            </motion.div>
          )}
        </AnimatePresence>

      </div>
    </div>
  );
}

// --- Sub-Components ---

const Input = ({ label, value, onChange, placeholder }: any) => (
  <div className="group">
    <label className="block text-[10px] font-bold text-slate-400 uppercase mb-1.5 ml-1 tracking-wider">{label}</label>
    <input
      type="text"
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="w-full bg-[#0F172A] border border-slate-700 rounded-lg px-3 py-2.5 text-[13px] text-slate-200 focus:border-[#00C26D]/50 focus:ring-1 focus:ring-[#00C26D]/50 outline-none transition-all placeholder-slate-600"
      placeholder={placeholder}
    />
  </div>
);

const Select = ({ label, value, options, onChange }: any) => (
  <div className="group">
    <label className="block text-[10px] font-bold text-slate-400 uppercase mb-1.5 ml-1 tracking-wider">{label}</label>
    <select
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="w-full bg-[#0F172A] border border-slate-700 rounded-lg px-3 py-2.5 text-[13px] text-slate-200 focus:border-[#00C26D]/50 outline-none appearance-none cursor-pointer"
    >
      {options.map((o: any) => <option key={o.value} value={o.value}>{o.label}</option>)}
    </select>
  </div>
);

const Toggle = ({ label, checked, onChange }: any) => (
  <div onClick={onChange} className={`flex items-center justify-between p-2 rounded-lg cursor-pointer border transition-all ${checked ? 'bg-[#00C26D]/5 border-[#00C26D]/30' : 'bg-transparent border-transparent hover:bg-slate-800'}`}>
    <span className={`text-[11px] font-medium ${checked ? 'text-white' : 'text-slate-400'}`}>{label}</span>
    <div className={`w-7 h-4 rounded-full relative transition-colors ${checked ? 'bg-[#00C26D]' : 'bg-slate-700'}`}>
      <div className={`absolute top-[2px] w-3 h-3 bg-white rounded-full transition-all ${checked ? 'left-[14px]' : 'left-[2px]'}`} />
    </div>
  </div>
);

const RewriteButton = ({ icon: Icon, label, onClick, disabled, isActive }: any) => (
  <button onClick={onClick} disabled={disabled} className={`flex items-center gap-1.5 px-3 py-1.5 rounded-lg text-[10px] font-medium transition-all ${isActive ? "bg-[#00C26D]/20 border border-[#00C26D] text-[#00C26D]" : "bg-slate-800 border border-slate-700 text-slate-400 hover:text-white"}`}>
    <Icon className="w-3 h-3" /> {label}
  </button>
);